import { CircleHelp } from 'lucide-react';
import { div as MDiv } from 'motion/react-client';
import { useInView } from 'motion/react';
import { useRef } from 'react';

export const HowWork = () => {
  const refTitle = useRef<HTMLDivElement | null>(null);
  const inView = useInView(refTitle);
  return <div className="w-full p-8 flex flex-col gap-4 items-center">
    <MDiv 
      ref={refTitle}
      className="flex gap-2 items-center text-[2.5rem] max-md:text-[2rem] text-primary"
      initial={{ opacity: 0, scale: 0 }}
      animate={inView ? { opacity: 1, scale: 1 } : false}
      transition={{
        duration: 0.4,
        scale: { type: 'spring', visualDuration: 0.4, bounce: 0.5 },
      }}
    >
      <CircleHelp className='w-[2.5rem] h-[2.5rem] mb-[-4px]' /> COMO FUNCIONA?
    </MDiv>
  </div>;
};